function isMobile(checkWhich = null) {
	var toMatch = [
        /Android/i,
        /webOS/i,
        /iPhone/i,
        /iPad/i,
        /iPod/i,
        /BlackBerry/i,
        /Windows Phone/i
    ];

    var mobile = toMatch.some(function(toMatchItem) {
        return navigator.userAgent.match(toMatchItem);
    });

	var whichMobile = toMatch;
	if(checkWhich == 'android') {
		whichMobile = [/Android/i];
	} else if(checkWhich == 'ios') {
		whichMobile =  [/iPhone/i, /iPad/i, /iPod/i, /iOS/i];
	}

	return !checkWhich ? mobile : mobile && whichMobile.some(function(toMatchItem) {
        return navigator.userAgent.match(toMatchItem);
    });
}

