import React, { useState, useEffect } from "react";

const useAudio = (url, playState = false) => {
    const [audio] = useState(new Audio(url));
    const [isPlaying, setPlaying] = useState(playState);

    const toggle = () => setPlaying(!isPlaying);

    useEffect(() => {
        isPlaying ? audio.play() : audio.pause();
        // eslint-disable-next-line
    }, [isPlaying]);

    useEffect(() => {
        audio.addEventListener('ended', () => setPlaying(isPlaying));
        return () => {
            audio.removeEventListener('ended', () => setPlaying(isPlaying));
        };
        // eslint-disable-next-line
    }, []);

    return [isPlaying, toggle];
};

const AudioPlayer = ({ url }) => {
    const [firstClick, setFirstClick] = useState(false);
    const clickNow = (e) => {
        // setTimeout(() => {
        //     if (e && !firstClick) {
        //         e.click();
        //         setFirstClick(true);
        //     }
        // }, 1000);
    }

    const [isPlaying, toggle] = useAudio(url);
    // console.log('playing', isPlaying)
    return (
        <div className="btn-audio">
            <button onClick={toggle} ref={clickNow}>{isPlaying ?
                <img src="./mute.png" alt="mute" style={{ width: 30, height: 'auto' }} />
                :
                <img src="./music.png" alt="music" style={{ width: 40, height: 'auto' }} />}

            </button>
        </div>
    );
};

export default AudioPlayer;
