import React, { Fragment, useState, useEffect } from 'react';

const CountDown = (props) => {
    let timer;
    let distance;

    const [remaining, remainingSet] = useState({
        hari: 0,
        jam: 0,
        minit: 0,
        saat: 0
    });
    const [isExpired, isExpiredSet] = useState(false);

    const setDate = (props) => {
        const { targetDate, targetTime } = props;
        // Get todays date and time
        const now = new Date().getTime();
        // Set the date we're counting down to
        const countDownDate = new Date(targetDate + " " + targetTime).getTime();

        // Find the distance between now and the count down date
        distance = countDownDate - now;

        // target date and time is less than current date and time
        if (distance < 0) {
            clearInterval(timer);
            isExpiredSet(true);
        } else {
            remainingSet({
                hari: Math.floor(distance / (1000 * 60 * 60 * 24)),
                jam: Math.floor(
                    (distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
                ),
                minit: Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60)),
                saat: Math.floor((distance % (1000 * 60)) / 1000)
            });
            isExpiredSet(false);
        }
    };

    const counter = (props) => {
        timer = setInterval(() => {
            setDate(props);
        }, 1000);
    };

    // Component mounted
    useEffect(() => {
        setDate(props);
        counter(props);

        return () => {
            clearInterval(timer);
        };
        // eslint-disable-next-line
    }, []);


    return (
        <Fragment>
            {!isExpired && props.targetDate && props.targetTime ? (
                <div className="counter">
                    {Object.entries(remaining).map((el, i) => (
                        <div key={i} className="entry">
                            <div key={el[1]} className="entry-value">
                                <span className="count top curr flipTop">{el[1] + 1}</span>
                                <span className="count top next">{el[1]}</span>
                                <span className="count bottom next flipBottom">{el[1]}</span>
                                <span className="count bottom curr">{el[1] + 1}</span>
                            </div>
                            <div className="entry-title">{el[0].toUpperCase()}</div>
                        </div>
                    ))}
                </div>
            ) : (
                    <p className="alert-danger">MAJLIS TELAH BERLANGSUNG</p>
                )}
        </Fragment>
    );
}
export default CountDown;
